<template>
  <div class="content">
    <keep-alive :include="[]">
      <router-view />
    </keep-alive>
  </div>
</template>

<script>
export default {
  data () {
    return {
      children: null
    };
  },
  beforeRouteEnter (to, from, next) {
    next((vm) => {
      const children = vm.$route.meta.children;
      if (children) {
        vm.children = children;
      } else {
        vm.children = null;
      }
    });
  },
  watch: {
    $route () {
      const children = this.$route.meta.children;
      if (children) {
        this.children = children;
      } else {
        this.children = null;
      }
    }
  }
};
</script>

